<template>
  <v-hover :style="cssProps">
    <v-card flat :class="`bordered grey ${hover ? 'lighten-3' : 'lighten-4'}`" slot-scope="{ hover }">
      <v-card-title primary-title class="headline pt-3 pb-0">
        <h3 class="headline mb-0">New Property</h3>
      </v-card-title>
      <v-card-text>
        <v-autocomplete
        :items="Object.keys(properties)"
        v-model="selectedProperty"
        label="Property"
        clearable
        >
          <v-btn
            slot="append-outer"
            icon
            @click="selectedProperty ? addProp(selectedProperty, value) : null"
          >
            <v-icon>add</v-icon>
          </v-btn>
        </v-autocomplete>
        <template v-if="selectedProperty">
          <v-layout row v-if="properties[selectedProperty].type == 'number'">
            <v-text-field
              label="Amount"
              v-model="amount"
              box
              color="primary"
              required
            >
            </v-text-field>
            <v-text-field
              label="Unit"
              :value="properties[selectedProperty].unit"
              readonly
              disabled
              class="bit pl-2"
              reverse
              outline
            >
            </v-text-field>
          </v-layout>
          <v-textarea
            v-else
            name="text"
            v-model="text"
            auto-grow
            box
            color="primary"
            label="Text"
            required
            counter
          />
        </template>
      </v-card-text>
    </v-card>
  </v-hover>
</template>

<script>
import properties from "@/components/ingredient/properties.js"

export default {
  props: [
    "addProp"
  ],
  computed: {
    value() {
      if (this.properties[this.selectedProperty].type == 'number') {
        return {
          "Amount": this.amount,
          "Unit": this.properties[this.selectedProperty].unit
        }
      } else {
        return this.text
      }
    },
    cssProps () {
      return {
        '--primary-color': this.$vuetify.theme.primary
      }
    }
  },
  data() {
    return {
      properties: properties,
      selectedProperty: null,
      text: "",
      amount: null,
    }
  }
}
</script>

<style scoped>
.bit {
  max-width: 75px;
} 
.bordered {
  border-left: 6px var(--primary-color) solid !important 
}
</style>