<template>
  <v-data-table
    :headers="headers"
    :items="items"
    :loading="loading"
    class="table"
    hide-actions
  >
    <template slot="items" slot-scope="props">
      <td>{{ props.item.name }}</td>
      <td>
        <v-edit-dialog
          :return-value.sync="props.item.amount"
          lazy
          @save="save"
        > {{ props.item.amount }}
          <v-text-field
            slot="input"
            v-model="props.item.amount"
            label="Edit"
            single-line
            counter
            @change="addProp(props.item.name, {Amount: props.item.amount, Unit: props.item.unit})"
          />
        </v-edit-dialog>
      </td>
      <td>{{ props.item.unit }}</td>
      <td>{{ props.item.type }}</td>
    </template>
  </v-data-table>
</template>

<script>

export default {
  props: [
    "loading",
    "items",
    "addProp"
  ],
  data() {
    return {
      headers: [
        {
          text: 'Property',
          align: 'left',
          value: 'name'
        },
        { text: 'Amount', value: 'amount' },
        { text: 'Unit', value: 'unit' },
        { text: 'Type', value: 'unit' },
      ],
    }
  },
  methods: {
    save() {
      return
    }
  },
}
</script>

<style>
.table {
  width: 70%;
}
</style>