<template>
  <div class="side">
    <basic-info
      :name="name"
      :description="description"
      @name="$emit('name', $event)"
      @description="$emit('description', $event)"
    />
    <new-property
      :addProp="addProp"
      :properties="properties"
    />
    <v-card flat>
      <v-card-text>
        <v-textarea
          v-for="item in textItems"
          :value="item[1]"
          :key="item[0]"
          auto-grow
          rows="3"
          color="primary"
          :label="item[0]"
          required
          counter
          @change="addProp(item[0], item[1])"
        >
          <v-btn
            slot="append-outer"
            icon
            @click="deleteProp(item[0])"
          >
            <v-icon>
              delete
            </v-icon>
          </v-btn>
        </v-textarea>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import NewProperty from "@/components/ingredient/sidepanel/NewProperty.vue"
import BasicInfo from "@/components/ingredient/sidepanel/BasicInfo.vue"

export default {
  components: {
    NewProperty,
    BasicInfo
  },
  props: [
    "name",
    "description",
    "properties",
    "addProp",
    "deleteProp",
    "textItems"
  ]
}
</script>

<style scoped>
.side {
  width: 30%;
  height: 100%;
  height: 100%;
  background: white;
}
</style>