<template>
  <v-layout row class="page">
    <side-panel
      :name="name"
      @name="name = $event"
      :description="description"
      @description="description = $event"
      :properties="properties"
      :addProp="addIngredient"
      :deleteProp="deleteIngredient"
      :textItems="Object.entries(data).filter(x => 
              x[1] && properties[x[0]].type == 'text')"
    />
    <display
      :loading="loading"
      :items="Object.entries(data).filter(x => 
              x[1] && properties[x[0]].type == 'number').map(x => 
                {return {name: x[0],amount: x[1].Amount,unit: x[1].Unit,type: properties[x[0]].group}})"
      :properties="properties"
      :deleteProp="deleteIngredient"
      :addProp="addIngredient"
    />
    <v-btn
      color="primary"
      bottom right
      fixed
      dark
      fab
      @click="saveIngredient()"
      slot="activator"
    >
      <v-icon>save</v-icon>
    </v-btn>
  </v-layout>
</template>

<script>
import properties from "@/components/ingredient/properties.js"
import SidePanel from "@/components/ingredient/sidepanel/SidePanel.vue"
import Display from "@/components/ingredient/Display.vue"
import { EventBus } from '@/plugins/event-bus.js';

export default {
  components: {
    SidePanel,
    Display
  },
  data() {
    return {
      data: {},
      name: "",
      description: "",
      loading: false,
      properties: properties,
    }
  },
  methods: {
    getIngredient() {
      console.log("Fetching ingredient...")
      this.loading = true
      this.$Parse.Cloud.run("get_ingredient", {object_id: this.$route.params.id}).then(response => {
        console.log(response)
        this.name = response.data.ingredient.get("name")
        this.description = response.data.ingredient.get("description")
        this.data = {
          ...Object.keys(properties).reduce((o, key) => Object.assign(o, {[key]: null}), {}),
          ...response.data.ingredient_versions.length == 0 ? 
            {} :
            response.data.ingredient_versions[response.data.ingredient_versions.length-1].get("properties")
        }
        this.loading = false
      }, error => {
        this.loading = false
        console.error(error)
      }); 
    },
    saveIngredient() {
      console.log("Saving ingredient...")
      console.log(this.name)
      console.log(this.description)
      console.log(this.data)
      this.loading = true
      this.$Parse.Cloud.run("post_ingredient_version", {object_id: this.$route.params.id, name: this.name, description: this.description, properties: this.data}).then(response => {
        console.log(response)
        this.name = response.data.ingredient.get("name")
        this.description = response.data.ingredient.get("description")
        this.data = {
          ...this.data,
          ...response.data.ingredient_version.get("properties")
        }
        this.loading = false
      }, error => {
        console.error(error)
        this.loading = false
      });
      EventBus.$emit('snackbar', "Saved successfully");
    },
    addIngredient(key, value) {
      console.log("Adding prop...")
      this.data[key] = value
    },
    deleteIngredient(key) {
      console.log("Deleting prop...")
      this.data[key] = null
    }
  },
  created() {
    let save = this.saveIngredient
    document.addEventListener("keydown", e => {
      if (e.keyCode == 83 && (navigator.platform.match("Mac") ? e.metaKey : e.ctrlKey)) {
        e.preventDefault();
        save()
      }
    }, false);
    this.getIngredient()
  }
}
</script>
<style scoped>
.page {
  height: 100%;
  width: 100%;
}
</style>